@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Nunito', sans-serif;
  font-size: 16px;
}

#root {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

p {
  font-size: 16px;
}

main {
  flex: 1 0 auto;
}

.container {
  margin: 0 auto;
  width: 85vw;
}

.container-padding {
  padding-top: 10px;
  padding-bottom: 10px;
}

.parallax-container {
  height: 80vh;
}

.mission-img {
  width: 100%;
}

.btn-margin {
  margin-bottom: 10px;
}
/* .hero-img {
  width: 100vw;
} */

.box-margin {
  margin-bottom: 0.5em;
}

.no-margin-bot {
  margin-bottom: 0;
}
.icon i {
  margin-right: 8px;
  font-size: 28px;
}

.icon {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}

.home-title {
  margin-top: 0;
}

.gray-background {
  background-color: #fafafa;
}

.map-container {
  margin: 2em auto;
  height: 70vh;
  width: 70%;
}

.organizations {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

.margin-auto {
  margin: 0 auto;
}

.flex-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 15px;
}

.box-container {
  /* background-color: #d4d4d4; */
  /* border: #888888 1px solid; */
  width: 15em;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 1em;
  display: flex;
  margin: 10px 15px;
}

/* .box-container:hover {
  box-shadow: 0 0 15px #888888;
} */

.box-content > img {
  width: 12em;
  height: 15em;
}

.box-content > p {
  text-align: center;
}

.flex {
  display: flex;
  flex-direction: column;
}

.font-big {
  font-weight: 600;
}

