.margin-auto {
  margin: 0 auto;
}

.flex-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 15px;
}

.box-container {
  /* background-color: #d4d4d4; */
  /* border: #888888 1px solid; */
  width: 15em;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 1em;
  display: flex;
  margin: 10px 15px;
}

/* .box-container:hover {
  box-shadow: 0 0 15px #888888;
} */

.box-content > img {
  width: 12em;
  height: 15em;
}

.box-content > p {
  text-align: center;
}

.flex {
  display: flex;
  flex-direction: column;
}

.font-big {
  font-weight: 600;
}
